import React,{Component} from "react";
import { Pagination } from "react-bootstrap";
import { GlobalContext } from "../../context/global-context";
import { SetPagination, SetIndex } from "../../actions/actions";
import {Page} from "../../components/page/page"



export class Filter extends Component{
    
    static contextType = GlobalContext;

    constructor(props){
        super(props);
        this.state = {page:[],filters:[]};
    }

    
    componentDidUpdate(prevProps,prevState,snapshots){
        

        let reindex = false;
        if(this.state.page != this.context.page){
            reindex = true;
        }
        if(this.state.filters != this.context.filters){
            reindex = true;
        }
        if(reindex){

            let index = this.context.page
                .map((line,index) => {
                    return this.context.filters && this.context.filters.regexFilter ? [index,this.context.filters.regexFilter.test(line)] : [index,true];
                });

            let lines = index.filter((iVal)=>iVal[1]);
            
            let pagination = this.context.pagination;
            let page = pagination.page;
            if(lines){
                let newPages = Math.ceil(lines / pagination.pageSize);
                if(pagination.page > newPages){
                  page = newPages; 
                }
            }else{
                page = 0;
            }

            this.context.dispatch(new SetIndex(index));
            this.context.dispatch(new SetPagination({lines:lines.length,page:page}));
        
            this.setState((state)=>{
                return {
                    page:this.context.page,
                    filters:this.context.filters
                }
            })
        }
    }

    render(){
        return  ( <Page></Page>);
    }

}